import { BasicColumn, FormSchema, useRender } from '@/components/Table';
import { defHttp } from '@/utils/http/axios';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getVehicleExecutionPage = '/po/vehicle-execution/page',
  // deleteVehicleExecution = '/po/vehicle-execution/delete',
  getVehicleData = '/po/vehicle-execution/get',
  exportVehicleExecution = '/po/vehicle-execution/export-excel',
}

// 查询
export function getVehicleExecutionPage(params) {
  return defHttp.get({ url: Api.getVehicleExecutionPage, params });
}
// 删除
// export function deleteVehicleExecution(id: string) {
//   return defHttp.delete({ url: Api.deleteVehicleExecution + '?id=' + id });
// }
export function getVehicleData(params) {
  return defHttp.get({ url: Api.getVehicleData, params });
}

// 导出
export function exportVehicleExecution(params) {
  return defHttp.download(
    { url: Api.exportVehicleExecution, params },
    { isReturnNativeResponse: true },
  );
}
const statusColor = {
  0: '#dc0000', // 草稿
  1: '#338ed1', // 处理中
  2: '#30ca5a', // 通过
  3: '#929292', // 不通过
  4: '#929292', // 已取消
  5: '#929292', // 已终止
  6: '#fda941', // 退回/驳回
};

